import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    musicInfoPath: null, //歌单基本信息请求地址
    musicCommentPath: null, //歌单请求评论地址
    musicianName: null, //搜索建议->歌手

    DjRecommendType: {}, //主播电台分类优秀新电台请求结果 
    DjRadioHot: {}, //主播电台分类电台排行榜请求结果

    frequency: [],

    playListPageCat: {},//歌单分类页的请求结果

    currentOffset: 1,
    
    mainNav: false,//导航条是否展开
    mainNavFlag: false,
    mainNavIndex: false,//导航条发现页


    total: 0,
    key: '',

    policyTip: true,//我的页面登录政策勾选
    policyTipShow: false,//未勾选政策提示
    loginShow: false, //窗口是否显示
    registerShow: false,//注册窗口是否显示
    userPhone: localStorage.getItem('userPhone'),
    userPassword: localStorage.getItem('userPassword'),
    userToken: '',
    userId: '',
    userTracks: 0,
    userPlayList: [],
    userPageFlag: false,//我的页面进入flag
    userAvatarUrl: '',
    userAutoLogin: localStorage.getItem('userAutoLogin'),//用户自动登录
    userDelTrack: 0,
    tipShow: false,//提示框
    createShow: false,//歌单新建框
    createFlag: false,//歌单新建选择
    createName: '',//歌单新建名
    operateShow: false,//选择框、创建歌单框
    operateFlag: false,//选择框、创建歌单框的选择
    operateInfo: '确定删除歌单？',
    windowHead: '登录',
    


    searchIndex: 1,
    searchCurrentCatName: 'singer',//当前搜索页分类名

    requestType: 'playlist',

    artistCatCurrntIndex: 0, //歌手分类当前索引

    type: '', //歌手分类字符串
    area: '', //歌手分类字符串

    nType: 0, //歌手分类数字
    nArea: 0,

    artistList: {}, //歌手分类默认数据

    singerHotSongs: [],//歌手热门歌曲

    descIsOpen: false,//单曲、歌单、专辑描述是否展开

    allAlbumInfo: [], //初始化新碟页面全部新碟信息，
    albumHotInfo: {}, //热门新碟
    albumCatInfo: {}, //全部新碟分类

    userRecordType: 1,//用户播放记录类型
    
    windowShow: false,//窗口是否显示

    offsetNum: 0,//偏移数

    musicIsCompleteUpdate: true,//页面是否完成更新
    
    
    musicPlayListCatName: '',//推荐页面歌单推荐分类名
    musicTopListCatName: 0,//推荐页面榜单分类名

    musicLyricIsPlay: false,//歌词是否播放

    musicAudioPlayStatus: false,

    musicAudioIndex: 0,// 正在播放音乐的索引

    musicAudioAddAllFlag: false,//歌单全部播放flag

    musicAudioId: 0,
    musicAudioIdList:[],

    musicAudioUrl: '',
    musicAudioUrlList: [],

    musicAudioDetail: {},
    musicAudioDetailList: [],

    musicAudioLyric: {},
    musicAudioLyricList: [],

    musicAudioList: []

  },
  getters: {
    // userPhone(){
    //   return localStorage.getItem('userPhone')
    // },
    // userPassword(){
    //   return localStorage.getItem('userPassword')
    // },
    // userAutoLogin(){
    //   return localStorage.getItem('userAutoLogin')
    // },
    // policyTip(){
    //   return localStorage.getItem('policyTip')
    // }
  },
  mutations: {
    changeValue(state, payload) {
      if (Array.isArray(payload)) {
        payload.forEach(item => {
          state[item.type] = item.value
        })
      } else {
        state[payload.type] = payload.value
      }
   
    },
    addValue(state, payload) {
      if (Array.isArray(payload)) {
        payload.forEach(item => {
          state[item.type].push(item.value)
        })
      } else {
        state[payload.type].push(payload.value)
      }

    },
  },
  actions: {},
  modules: {}
})