<template>
  <div class="addwindow">
    <div class="awadd">
      <img src="../../../assets/login/newplaylist.png" />
      <span>新歌单</span>
    </div>
    <div class="choosemain">
      <div
        class="chooseitem"
        v-for="(value, index) in userPlayList"
        :key="index"
        @click="playListTracks('add', value.id, userTracks)"
      >
        <img :src="value.coverImgUrl" />
        <div class="chooseinfo">
          <span>{{value.name}}</span>
          <span>{{value.trackCount}}首</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from "../../../network/request";
export default {
  name: "AddWindow",
  data() {
    return {
      userPlayList: [],
    };
  },
  computed: {
    userId() {
      return this.$store.state.userId;
    },
    userTracks() {
      return this.$store.state.userTracks;
    },
  },
  created() {
    this.getUserPlayList(this.userId);
  },
  methods: {
    getUserPlayList(id) {
      request({
        url: `/user/playlist?uid=${id}`,
      }).then((response) => {
        response.playlist.forEach((ele) => {
          if (ele.creator.userId === id) {
            this.userPlayList.push(ele);
          }
        });
      });
    },
    playListTracks(op, pid, tracks) {
      request({
        url: `/playlist/tracks?op=${op}&pid=${pid}&tracks=${tracks}`,
      }).then((response) => {
        console.log(response);
        this.$store.commit("changeValue", [
          {
          type: "tipShow",
          value: true,
        },
        {
          type: 'loginShow',
          value: false
        }
        ]);

        setTimeout(() => {
          this.$store.commit("changeValue", {
            type: "tipShow",
            value: false,
          });
        }, 2000);
      });
    },
  },
};
</script>

<style>
.awadd {
  background-color: #e6e6e6;
  height: 55px;
  position: relative;
  vertical-align: center;
  font-size: 12px;
  padding-left: 40px;
  cursor: pointer;
}
.awadd > img {
  position: relative;
  top: 10px;
  margin-right: 10px;
}

.chooseinfo > span:first-of-type {
  margin-bottom: 8px;
}
.chooseinfo {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: 10px;
}
.chooseitem > img {
  width: 40px;
  height: 40px;
}
.chooseitem:hover {
  background-color: #f2f2f2;
}
.chooseitem {
  display: flex;
  color: #333;
  padding: 7px 0;
  padding-left: 40px;
  font-size: 12px;
  border-bottom: solid 1px #e0e0e0;
  cursor: pointer;
}
</style>