<template>
  <div
    class="loginmain"
    :style="{'top': `${offsetTop}px`, 'left': `${offsetLeft}px`}"
    v-show="loginShow"
    @mousemove="movePosition"
    @mouseup="upPosition"
    @mouseleave="leavePosition"
  >
    <div class="pwhead" @mousedown="downPosition($event)">
      <span draggable="false">{{windowHead}}</span>
      <span @click="toggleLoginShow">X</span>
    </div>

    <div class="pwmain">
      <add-window v-if="userId" />
      <login-register v-else-if="registerShow" />
      <login-window v-else />
    </div>
    
  </div>
</template>
<script>
import LoginWindow from "./LoginWindow/LoginWindow";
import AddWindow from "./AddWindow/AddWindow";
import LoginRegister from "./LoginWindow/LoginRegister"
export default {
  name: "PopupWindow",
  components: {
    LoginWindow,
    AddWindow,
    LoginRegister
  },

  data() {
    return {
      clientX: 0,
      clientY: 0,
      offsetX: 0,
      offsetY: 0,
      offsetLeft: 720,
      offsetTop: 330,
      saveX: 720,
      saveY: 330,
      flag: false,
      // phoneLogin: false,
    };
  },
  computed: {
    topOffset() {
      let sY = this.saveY;
      let initY = sY - this.offsetY;
      if (this.flag) {
        return initY;
      }
    },
    leftOffset() {
      let sX = this.saveX;
      let initX = sX - this.offsetX;
      if (this.flag) {
        return initX;
      }
    },
    loginShow() {
      return this.$store.state.loginShow;
    },
    windowHead() {
      return this.$store.state.windowHead
    },
    userId() {
      return this.$store.state.userId;
    },
    registerShow(){
      return this.$store.state.registerShow
    }

  },
  methods: {
    downPosition(event) {
      this.flag = true;
      this.clientX = event.clientX;
      this.clientY = event.clientY;
    },
    movePosition() {
      if (this.flag) {
        this.offsetX = this.clientX - event.clientX;
        this.offsetY = this.clientY - event.clientY;

        this.offsetTop = this.saveY - this.offsetY;
        this.offsetLeft = this.saveX - this.offsetX;
      } else {
        this.saveY = this.offsetTop;
        this.saveX = this.offsetLeft;
      }
    },
    upPosition() {
      this.flag = false;
    },
    leavePosition() {
      this.flag = false;
    },
    changeLogin(value) {
      this.phoneLogin = value;
    },
    toggleLoginShow() {
      this.$store.commit("changeValue", {
        type: "loginShow",
        value: !this.loginShow,
      });
    },
  },
};
</script>
<style scoped>
.pwmain {
  height: 335px;
  overflow: auto;
}
.pwhead {
  color: #fff;
  height: 38px;
  background-color: #2d2d2d;
  display: flex;
  justify-content: space-between;
  line-height: 38px;
  padding: 0 20px;
  cursor: move;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.pwhead > span:first-of-type {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.pwhead > span:last-of-type {
  color: #888;
  font-weight: bold;
  cursor: pointer;
}
.loginmain {
  z-index: 999;
  width: 530px;
  height: 370px;
  background-color: #fff;
  margin: auto;
  position: fixed;
  box-shadow: -1px 0px 32px -11px #000700;
  font-size: 14px;
  border-radius: 5px;
  top: 330px;
  left: 720px;
}
</style>