<template>
  <div class="useroperate" v-if="userToken">
    <div
      class="useritem"
      v-for="(value, index) in userArray"
      :key="index"
      @click="jumpPage('profile', userId, index)"
    >
      <img :src="value.img" alt />
      <span>{{value.op}}</span>
    </div>
  </div>
</template>

<script>
import userprofile from "../../assets/user/userprofile.png";
import usermsg from "../../assets/user/usermsg.png";
import userlevel from "../../assets/user/userlevel.png";
import uservip from "../../assets/user/uservip.png";
import userset from "../../assets/user/userset.png";
import userver from "../../assets/user/userver.png";
import userout from "../../assets/user/userout.png";

import { request } from "../../network/request";
export default {
  name: "UserOperate",
  data() {
    return {
      userArray: [
        { img: userprofile, op: "我的主页" },
        { img: usermsg, op: "我的消息" },
        { img: userlevel, op: "我的等级" },
        { img: uservip, op: "VIP会员" },
        { img: userset, op: "个人设置" },
        { img: userver, op: "实名认证" },
        { img: userout, op: "退出" },
      ],
    };
  },
  computed: {
    userId() {
      return this.$store.state.userId;
    },
    userToken() {
      return this.$store.state.userToken;
    },
  },
  methods: {
    jumpPage(path, id, index) {
      if (index === 0) {
        this.$router.push({
          name: path,
          params: {
            id: id,
          },
        });
      }
      if (index === 6) {
        request({
          url: "/logout",
        }).then((response) => {
          this.$store.commit("changeValue", [
            {
              type: "userToken",
              value: "",
            },
            {
              type: "userId",
              value: "",
            },
            {
              type: "userTracks",
              value: 0,
            },
            {
              type: "userPlayList",
              value: [],
            },
            {
              type: "userAvatarUrl",
              value: "",
            },
          ]);
          localStorage.clear();
        });
      }
    },
  },
};
</script>

<style scoped>
.useritem > img {
  position: relative;
  top: 3px;
  margin-right: 15px;
}
.useritem {
  padding-left: 30px;
  height: 34px;
  line-height: 34px;
  font-size: 12px;
  color: #ccc;
  position: relative;
}
.useritem:hover {
  color: #fff;
  background-color: #353535;
  cursor: pointer;
}
.useroperate {
  width: 160px;
  height: 242px;
  background-color: #2b2b2b;
  border-radius: 5px;
  position: absolute;
  top: 60px;
  left: 1390px;
  z-index: -1;
  opacity: 0;
}
.useroperate:hover {
  opacity: 1;
  z-index: 999;
}
</style>