<template>
  <div
    class="cwmain"
    :style="{'top': `${offsetTop}px`, 'left': `${offsetLeft}px`}"
    v-show="createShow"
    @mousemove="movePosition"
    @mouseup="upPosition"
    @mouseleave="leavePosition"
  >
    <div class="cwhead" @mousedown="downPosition($event)">
      <span draggable="false">{{windowHead}}</span>
      <span @click="toggleCreateShow">X</span>
    </div>

    <div class="cwinp">
      <span>歌单名：</span>
      <input type="text" v-model="createName" />
    </div>

    <div :style="alert?'opacity: 1':'opacity: 0'" class="cwalert"><img src="../../../assets/user/limitchart.png" alt=""></div>

    <div class="cwitem">
      <div class="cwinfo">{{createInfo}}</div>
      <div class="cwchoose">
        <div class="cwdetermine" @click="chooseOperate(true)">确定</div>
        <div class="cwabolish" @click="chooseOperate(false)">取消</div>
      </div>
    </div>
  </div>
</template>
<script>
import {request} from "../../../network/request"
export default {
  name: "PopupWindow",
  data() {
    return {
      clientX: 0,
      clientY: 0,
      offsetX: 0,
      offsetY: 0,
      offsetLeft: 720,
      offsetTop: 330,
      saveX: 720,
      saveY: 330,
      flag: false,

      total: 0,
      alert: false,
      createName: "",
      createInfo: "可通过“收藏”将音乐添加到新歌单中",
      windowHead: "新建歌单",
    };
  },
  computed: {
    topOffset() {
      let sY = this.saveY;
      let initY = sY - this.offsetY;
      if (this.flag) {
        return initY;
      }
    },
    leftOffset() {
      let sX = this.saveX;
      let initX = sX - this.offsetX;
      if (this.flag) {
        return initX;
      }
    },
    createShow() {
      return this.$store.state.createShow;
    },
  },
  watch: {
    createName(newVal, oldVal) {
      let nameArray = this.createName.split("");
      let count = 0;
      nameArray.forEach((ele) => {
        if (ele.charCodeAt(0)<123) {
          count += 1;
        } else {
          count += 2;
        }
      });
      this.total = count;
      if (this.total > 40) {
        let len = this.createName.length
        this.alert = true;
        setTimeout(() => {
          this.alert = false;
        }, 1000);
        this.createName = oldVal
      }
    },
  },
  methods: {
    downPosition(event) {
      this.flag = true;
      this.clientX = event.clientX;
      this.clientY = event.clientY;
    },
    movePosition() {
      if (this.flag) {
        this.offsetX = this.clientX - event.clientX;
        this.offsetY = this.clientY - event.clientY;

        this.offsetTop = this.saveY - this.offsetY;
        this.offsetLeft = this.saveX - this.offsetX;
      } else {
        this.saveY = this.offsetTop;
        this.saveX = this.offsetLeft;
      }
    },
    upPosition() {
      this.flag = false;
    },
    leavePosition() {
      this.flag = false;
    },
    chooseOperate(flag) {

      if(flag === true){
        request({
          url: `/playlist/create?name=${this.createName}`
        }).then(response => {
          console.log(response);
        })
      }
      this.$store.commit("changeValue", [
        {
          type: "createShow",
          value: false,
        },
        {
          type: "createFlag",
          value: flag,
        }
      ]);

      this.$router.go(0)
    },
    toggleCreateShow() {
      this.$store.commit("changeValue", {
        type: "createShow",
        value: !this.createShow,
      });
    },
  },
};
</script>
<style scoped>
.cwalert {
  margin-left: 75px;
  margin-top: 10px;
}
.cwinp > input {
  width: 314px;
  height: 30px;
  outline: none;
  border: #cdcdcd solid 1px;
  padding: 0 10px;
}
.cwinp {
  font-size: 12px;
  color: #333;
  margin-top: 40px;
  margin-left: 26px;
}
.cwdetermine {
  background: linear-gradient(0deg, #1d6ebe, #4493dc);
  color: #fff;
}
.cwdetermine:hover {
  background: linear-gradient(0deg, #3382ce, #5ea3e3);
}
.cwabolish {
  background: linear-gradient(0deg, #f1f1f1, #fff);
  border: solid 1px #c3c3c3;
}
.cwabolish:hover {
  background: linear-gradient(0deg, #f8f8f8, #fff);
  border: solid 1px #c6c6c6;
}
.cwchoose > div {
  width: 80px;
  height: 31px;
  border-radius: 3px;
  line-height: 31px;
  margin: 10px;
  cursor: pointer;
}
.cwchoose {
  display: flex;
  margin-left: 65px;
  margin-top: 10px;
}
.cwinfo {
  margin-top: 10px;
  margin-left: -125px;
  font-size: 12px;
  color: #999;
}
.cwitem {
  margin-top: 10px;
  text-align: center;
}
.cwhead {
  color: #fff;
  height: 38px;
  background-color: #2d2d2d;
  display: flex;
  justify-content: space-between;
  line-height: 38px;
  padding: 0 20px;
  cursor: move;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.cwhead > span:first-of-type {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.cwhead > span:last-of-type {
  color: #888;
  font-weight: bold;
  cursor: pointer;
}
.cwmain {
  z-index: 999;
  width: 480px;
  height: 248px;
  background-color: #fff;
  margin: auto;
  position: fixed;
  box-shadow: -1px 0px 32px -11px #000700;
  font-size: 14px;
  border-radius: 5px;
  top: 330px;
  left: 720px;
  overflow: auto;
}
</style>