<template>
  <div class="phonemain">
    <div class="phonetop">
      <div class="user">
        <div class="prefix">+86</div>
        <input type="text" placeholder="请输入手机号" v-model="phone" />
      </div>
      <div class="pwd">
        <input type="password" placeholder="请输入密码" v-model="password" @keydown.enter="phoneLogin"/>
      </div>
      <div class="lptip" :style="loginError?'opacity: 1':'opacity: 0'">
        <img src="../../../assets/login/phonetip.png" alt="">
      </div>
      <div class="pop">
        <div class="auto">
          <input type="checkbox" v-model="autoLogin"/>
          <span>自动登录</span>
        </div>
        <div class="forget">
          <span>忘记密码？</span>
        </div>
      </div>
      <div class="pbtn" @click="phoneLogin">登录</div>
      <div class="phonebottom">
        <div @click="goBack">&lt; 其他登录方式</div>
        <div>没有帐号？免费注册 &gt;</div>
      </div>
      <!-- <div @click="loginStatus"> 状态</div> -->
    </div>
  </div>
</template>
<script>
import { request } from "../../../network/request";
export default {
  name: "LoginPhone",
  data() {
    return {
      // phone: 18908077873,
      // password: "123yqgta",
      phone: null,
      password: '',
      loginInfo: {},
      loginError: false
    };
  },
  computed: {
    autoLogin: {
      get(){
        return this.$store.state.userAutoLogin
      },
      set(val){
        localStorage.setItem('userAutoLogin', val);
      }
    },
  },
  methods: {
    phoneLogin() {

      if(this.autoLogin){
        localStorage.setItem('userPhone', this.phone);
        localStorage.setItem('userPassword', this.password);
      }

      request({
        url: `/login/cellphone?phone=${this.phone}&password=${this.password}`,
      }).then((response) => {
        // console.log(response);
        this.loginInfo = response;
        this.$store.commit('changeValue',{
          type: "userAvatarUrl",
          value: response.profile.avatarUrl,
        });

        request({
          url: `/user/playlist?uid=${response.profile.userId}`,
        }).then((response) => {

          this.loginError = false
          
          this.$store.commit("changeValue", {
            type: "userPlayList",
            value: response.playlist,
          });
        });

        this.$store.commit("changeValue", [
          {
            type: "userToken",
            value: response.token,
          },
          {
            type: "loginShow",
            value: false,
          },
          {
            type: "userId",
            value: response.profile.userId,
          },
        ]);
        console.log(response);
      }).catch(error => {
        this.loginError = true
      })
    },
    loginStatus() {
      return request({
        url: `/login/status`,
      }).then((response) => {
        console.log(response);
      });
    },
    goBack() {
      this.$emit("backIndex", false);
      this.$store.commit("changeValue", {
        type: "windowHead",
        value: "登录",
      });
    },
  },
};
</script>
<style scoped>
.lptip {
  margin-top: 10px;
}
.phonebottom > div:first-of-type {
  color: #0c72c3;
  cursor: pointer;
}
.phonebottom > div:last-of-type {
  color: #333;
  cursor: pointer;
}
.phonebottom {
  margin-top: 70px;
  margin-left: -154px;
  width: 488px;
  height: 49px;
  background-color: #f7f7f7;
  border-top: solid 1px #c6c6c6;
  display: flex;
  line-height: 49px;
  padding: 0 20px;
  justify-content: space-between;
}
.pbtn {
  height: 31px;
  line-height: 31px;
  text-align: center;
  background: linear-gradient(0deg, #1d6ebe, #4493dc);
  color: #fff;
  border: solid 1px #0e65bc;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 20px;
}
.pbtn:hover {
  background: linear-gradient(0deg, #3382ce, #5ea3e3);
  border: solid 1px #1860c8;
}
.forget:hover > span {
  cursor: pointer;
  border-bottom: solid 1px;
}
.pop {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  position: relative;
}
.auto > input {
  margin-right: 10px;
  position: relative;
  top: 2px;
}
.pwd > input {
  width: 198px;
  margin-top: 10px;
  height: 32px;
}
input {
  border: solid 1px #cdcdcd;
  outline: none;
  padding: 0 10px;
}
.user {
  display: flex;
}
.prefix {
  height: 28px;
  line-height: 28px;
  width: 42px;
  border: solid 1px #cdcdcd;
  border-right: none;
}
.phonetop {
  width: 220px;
  height: 224px;
  margin: 0 auto;
  font-size: 12px;
  color: #333;
  margin-top: 30px;
}
</style>