<template>
  <div class="lrmain">
    <div class="lr">
      <div class="lrphone">手机号：</div>
      <div class="lrcode">+86</div>
      <input type="text" placeholder="请输入手机号" class="lrinp1" v-model="regPhone" />
      <div class="lrpwd">密码：</div>
      <input type="text" placeholder="设置登录密码，不少于6位" class="lrinp2" v-model="regPassword" />
      <div :class="expPhone && expPassword?'lrnext lrok':'lrnext'">下一步</div>
    </div>
    <div class="lrpre" @click="preLogin">
      <span>&lt; 返回登录</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "LoginRegister",
  data() {
    return {
      phoneExp: /^1[0-9]{10}$/,
      passwordExp: /(?!^\\d+$)(?!^[a-zA-Z]+$)(?!^[_#@]+$).{6,}/,
      regPhone: null,
      regPassword: null,
    };
  },
  computed: {
    expPhone() {
      if (this.phoneExp.test(this.regPhone)) {
        return true;
      } else {
        return false;
      }
    },
    expPassword() {
      if (this.passwordExp.test(this.regPassword)) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    preLogin() {
      this.$store.commit("changeValue", {
        type: "registerShow",
        value: false,
      });
    },
  },
};
</script>
<style scoped>
.lrpre {
  cursor: pointer;
}
.lrpre {
  width: 490px;
  height: 49px;
  margin-top: 78px;
  line-height: 49px;
  background-color: #f7f7f7;
  padding: 0 20px;
  color: #0c72c3;
  border-top: solid 1px #c6c6c6;
}
.lrnext {
  margin-top: 20px;
  width: 240px;
  height: 31px;
  text-align: center;
  line-height: 31px;
  border: solid 1px #ccc;
  background: linear-gradient(0deg, #e7e7e7, #f5f5f5);
  cursor: default;
  color: #bebebe;
  border-radius: 5px;
}
.lrok {
  background: linear-gradient(0deg, #1d6ebe, #4493dc);
  color: #fff;
  border: solid 1px #3984ce;
  cursor: pointer;
}
.lrok:hover {
  background: linear-gradient(0deg, #3382ce, #5ea3e3);
}
.lrinp2 {
  width: 220px;
  height: 28px;
  padding: 0 10px;
  border: solid 1px #cdcdcd;
  outline: none;
}
.lrpwd {
  margin-top: 10px;
  margin-bottom: 10px;
}
.lrinp1 {
  width: 182px;
  height: 28px;
  border: solid 1px #cdcdcd;
  border-left: none;
  padding: 0 10px;
  outline: none;
}
.lrcode {
  line-height: 28px;
  text-align: center;
  width: 38px;
  height: 28px;
  border: solid 1px #cdcdcd;
  display: inline-block;
}
.lrphone {
  margin-bottom: 10px;
}
.lrmain {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  font-size: 12px;
  color: #666;
}
</style>