<template>
  <div class="tipwindow" v-show="tipShow">
    <div class="tipok" >
      <img src="../../../assets/login/OK.png">
      <span>收藏成功</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TipWindow',
  computed: {
    tipShow(){
      return this.$store.state.tipShow
    }
  },
  watch: {
    tipShow(newVal){
      if(newVal === true){
        setTimeout(() => {
          this.$store.commit('changeValue', {
            type: 'tipShow',
            value: false
          })
        }, 2000)
      }
    }
  }
}
</script>

<style>
.tipok {
  line-height: 52px;
}
.tipok >img{
  margin-right: 10px;
  position: relative;
  top: 4px;
}
.tipwindow {
  text-align: center;
  width: 280px;
  height: 50px;
  font-size: 12px;
  background-color: #fff;
  border: solid 1px #c0c0c0;
  box-shadow: 0px 0px 33px -14px #01110C;
  position: fixed;
  left: 45%;
  top: 45%;
  z-index: 99;
}
</style>