<template>
  <div
    class="owmain"
    :style="{'top': `${offsetTop}px`, 'left': `${offsetLeft}px`}"
    v-show="operateShow"
    @mousemove="movePosition"
    @mouseup="upPosition"
    @mouseleave="leavePosition"
  >
    <div class="owhead" @mousedown="downPosition($event)">
      <span draggable="false">{{windowHead}}</span>
      <span @click="toggleoperateShow">X</span>
    </div>

    <div class="owitem">
      <div class="owinfo">{{operateInfo}}</div>
      <div class="owchoose">
        <div class="determine" @click="chooseOperate(true)">确定</div>
        <div class="abolish" @click="chooseOperate(false)">取消</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PopupWindow",
  components: {},
  data() {
    return {
      clientX: 0,
      clientY: 0,
      offsetX: 0,
      offsetY: 0,
      offsetLeft: 720,
      offsetTop: 330,
      saveX: 720,
      saveY: 330,
      flag: false,
    };
  },
  computed: {
    topOffset() {
      let sY = this.saveY;
      let initY = sY - this.offsetY;
      if (this.flag) {
        return initY;
      }
    },
    leftOffset() {
      let sX = this.saveX;
      let initX = sX - this.offsetX;
      if (this.flag) {
        return initX;
      }
    },
    operateShow() {
      return this.$store.state.operateShow;
    },
    operateInfo(){
      return this.$store.state.operateInfo
    },
    windowHead() {
      return this.$store.state.windowHead;
    },
  },
  methods: {
    downPosition(event) {
      this.flag = true;
      this.clientX = event.clientX;
      this.clientY = event.clientY;
    },
    movePosition() {
      if (this.flag) {
        this.offsetX = this.clientX - event.clientX;
        this.offsetY = this.clientY - event.clientY;

        this.offsetTop = this.saveY - this.offsetY;
        this.offsetLeft = this.saveX - this.offsetX;
      } else {
        this.saveY = this.offsetTop;
        this.saveX = this.offsetLeft;
      }
    },
    upPosition() {
      this.flag = false;
    },
    leavePosition() {
      this.flag = false;
    },
    chooseOperate(flag) {
      this.$store.commit("changeValue", [
        {
          type: "operateShow",
          value: false,
        },
        {
          type: "operateFlag",
          value: flag,
        },
      ]);
    },
    toggleoperateShow() {
      this.$store.commit("changeValue", {
        type: "operateShow",
        value: !this.operateShow,
      });
    },
  },
};
</script>
<style scoped>
.determine {
  background: linear-gradient(0deg, #1d6ebe, #4493dc);
  color: #fff;
}
.determine:hover {
  background: linear-gradient(0deg, #3382ce, #5ea3e3);
}
.abolish {
  background: linear-gradient(0deg, #f1f1f1, #fff);
  border: solid 1px #c3c3c3;
}
.abolish:hover {
  background: linear-gradient(0deg, #f8f8f8, #fff);
  border: solid 1px #c6c6c6;
}
.owchoose > div {
  width: 80px;
  height: 31px;
  border-radius: 3px;
  line-height: 31px;
  margin: 10px;
  cursor: pointer;
}
.owchoose {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.owinfo {
  margin-top: 40px;
}
.owitem {
  text-align: center;
}
.owhead {
  color: #fff;
  height: 38px;
  background-color: #2d2d2d;
  display: flex;
  justify-content: space-between;
  line-height: 38px;
  padding: 0 20px;
  cursor: move;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.owhead > span:first-of-type {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owhead > span:last-of-type {
  color: #888;
  font-weight: bold;
  cursor: pointer;
}
.owmain {
  z-index: 999;
  width: 480px;
  height: 200px;
  background-color: #fff;
  margin: auto;
  position: fixed;
  box-shadow: -1px 0px 32px -11px #000700;
  font-size: 14px;
  border-radius: 5px;
  top: 330px;
  left: 720px;
  overflow: auto;
}
</style>