<template>
  <div :style="mainNav?minHeight:height">
    <nav>
      <nav-item
        v-for="(item, index) in navList"
        :key="index"
        @click.native="navChange(index)"
        :style="mainNav?opacityOne:opacityZero"
      >
        <span :class="currentIndex == index?'active':''" id="item">{{item}}</span>
      </nav-item>
    </nav>
  </div>
</template>

<script>
import Nav from "./Nav";
import NavItem from "./NavItem";
export default {
  name: "MainNav",
  components: {
    Nav,
    NavItem,
  },
  data() {
    return {
      navList: ["推荐", "排行榜", "歌单", "主播电台", "歌手", "新碟上架"],
      pathList: [
        "recommend",
        "toplist",
        "playlistcat",
        "djradio",
        "artlist",
        "album",
      ],
      currentIndex: sessionStorage.getItem("NavCurrentIndex"),
      minHeight: { height: "34px", backgroundColor: "#c20c0c" },
      height: { height: "5px", backgroundColor: "#c20c0c" },
      opacityOne: { opacity: 1 },
      opacityZero: { opacity: 0 },
    };
  },
  computed: {
    mainNav() {
      return this.$store.state.mainNav;
    },
    mainNavIndex(){
      return this.$store.state.mainNavIndex
    }
  },
  watch: {
    mainNavIndex(newVal){
      if(newVal === true){
        this.currentIndex = 0
        this.$store.commit('changeValue', {
          type: 'mainNavIndex',
          value: false
        })
      }
    }
  },
  created() {
    if (sessionStorage.getItem("NavCurrentIndex")) {
    } else {
      this.currentIndex = 0;
    }

    this.pathList.forEach((ele, index) => {
      if (`/discover/${ele}` == window.location.pathname) {
        this.$store.commit("changeValue", {
          type: "mainNav",
          value: true,
        });
      }
    });


    // if (window.location.pathname === "/discover/recommend") {
    //   this.$store.commit("changeValue", {
    //     type: "mainNav",
    //     value: true,
    //   });
    // } else {
    //   this.$store.commit("changeValue", {
    //     type: "mainNav",
    //     value: false,
    //   });
    // }
  },

  methods: {
    navChange(index) {
      this.currentIndex = index;
      this.$store.commit("changeValue", {
        type: "mainNavFlag",
        value: true,
      });
      sessionStorage.setItem("NavCurrentIndex", index);
      this.$router.push("/discover/" + this.pathList[index]);
    },
  },
};
</script>

<style scoped>
.main {
  background-color: #c20c0c;
  min-height: 5px;
}

.false {
  height: 5px;
}

.navfa {
  opacity: 0;
}

nav {
  display: flex;
  justify-content: center;
  color: #fff;
  margin-left: -80px;
}

#item {
  padding: 2px 7px;
  border-radius: 20px;
  transition: 0.1s;
  position: relative;
}
#item:hover {
  background-color: #9b0909;
  cursor: pointer;
}
#item.active {
  background-color: #9b0909;
}
</style>