<template>
  <div>
    <login-phone v-if="phoneLogin" @backIndex="changeLogin" />
    <login-index v-else @jumpLogin="changeLogin" />
  </div>
</template>
<script>
import LoginIndex from "./LoginIndex";
import LoginPhone from "./LoginPhone";
export default {
  name: "LoginWindow",
  components: {
    LoginIndex,
    LoginPhone,
  },
  data() {
    return {
      phoneLogin: false,
    };
  },
  computed: {
    loginShow() {
      return this.$store.state.loginShow;
    },
  },
  methods: {
    changeLogin(value) {
      this.phoneLogin = value;
    },
    toggleLoginShow() {
      this.$store.commit("changeValue", {
        type: "loginShow",
        value: !this.loginShow,
      });
    },
  },
};
</script>
<style scoped>
.head {
  color: #fff;
  height: 38px;
  background-color: #2d2d2d;
  display: flex;
  justify-content: space-between;
  line-height: 38px;
  padding: 0 20px;
  cursor: move;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.head > span:first-of-type {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.head > span:last-of-type {
  color: #888;
  font-weight: bold;
  cursor: pointer;
}
</style>