<template>
  <div>
    <tab-bar class="tabBar">
      <img src="../../assets/topbar.jpg" />
      <tab-bar-item
        v-for="(item, index) in tabList"
        :key="index"
        @click.native="activeChange(index)"
        :class="index==currentIndex?'active tbi':'tbi'"
      >
        {{item}}
        <span :class="index==currentIndex?'active':''"></span>
      </tab-bar-item>
      <search />
      <div class="center">创作者中心</div>

      <a @click="login" v-if="!userToken">登录</a>

      <div v-else class="userava">
        <div>
          <img :src="userAvatarUrl" />
        </div>
      </div>
      <user-operate class="uo" />
    </tab-bar>
  </div>
</template>

<script>
import TabBar from "./TabBar";
import TabBarItem from "./TabBarItem";
import Search from "../Search/Search";
import UserOperate from "../Operate/UserOperate";

export default {
  name: "MainTabBar",
  components: {
    TabBar,
    TabBarItem,
    Search,
    UserOperate,
  },

  data() {
    return {
      tabList: ["发现音乐", "我的音乐", "朋友", "商城", "音乐人", "下载客户端"],
      tabPath: ["recommend", "my", "friend", "market", "musician", "download"],
      currentIndex: sessionStorage.getItem("TabBarCurrentIndex"),
      isShow: false,
      count: 0,
    };
  },
  created() {
    if (sessionStorage.getItem("TabBarCurrentIndex")) {
    } else {
      this.currentIndex = 0;
    }
  },
  computed: {
    userToken() {
      return this.$store.state.userToken;
    },
    userAvatarUrl() {
      return this.$store.state.userAvatarUrl;
    },
  },
  methods: {
    activeChange(index) {
      this.currentIndex = index;
      sessionStorage.setItem("TabBarCurrentIndex", index);

      this.$router.push({
        name: this.tabPath[index],
      });
      
      this.$store.commit('changeValue', {
        type: 'mainNavIndex',
        value: true
      })

      if (index === 0) {
        this.$store.commit("changeValue", {
          type: "mainNav",
          value: true,
        });
      } else {
        this.$store.commit("changeValue", {
          type: "mainNav",
          value: false,
        });
      }

    },
    login() {
      if (this.userToken) {
      } else {
        this.$store.commit("changeValue", {
          type: "loginShow",
          value: true,
        });
      }
    },
  },
};
</script>

<style scoped>
.userava:hover + .uo {
  opacity: 1;
  z-index: 999;
}
.userava {
  height: 50px;
  width: 30px;
  position: relative;
  left: 20px;
  top: 20px;
}
.userava > div {
  height: 30px;
  width: 30px;
  overflow: hidden;
  border-radius: 30px;
}
.userava > div > img {
  width: 30px;
  height: 30px;
}
span {
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  line-height: 16px;
  border: 8px solid;
  border-color: transparent;
  position: absolute;
  top: 55px;
  left: 40%;
}

span.active {
  border-color: transparent transparent #c20c0c;
}
.tabBar {
  display: flex;
  justify-content: center;
  font-size: 14px;
  color: #999999;
  background-color: #242424;
}

img {
  padding-right: 20px;
}

.active {
  background-color: #000;
}

.center {
  border: 1px solid #4f4f4f;
  padding: 0 16px;
  border-radius: 20px;
  height: 32px;
  font-size: 5px;
  color: #ccc;
  display: flex;
  align-items: center;
  margin: 19px 0 0 12px;
  cursor: pointer;
  background-color: #242424;
}

.center:hover {
  border: 1px solid #ccc;
}

a {
  font-size: 12px;
  margin-top: 28px;
  margin-left: 20px;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  text-decoration: underline;
  color: #787878;
}
.tbi {
  color: #ccc;
}
.tbi:hover {
  color: #fff;
}

.active {
  color: #fff;
}
</style>