<template>
  <div class="mian">
    <div class="top">
      <div class="left">
        <img src="../../../assets/login/logineq.png" />
        <div class="login">
          <span @click="login('login')">手机号码登录</span>
        </div>
        <div class="register" @click="login('register')">
          <span>注册</span>
        </div>
      </div>
      <div class="right">
        <img src="../../../assets/login/otherlogin.png" alt />
      </div>
    </div>
    <div class="bottom">
      <input type="checkbox" v-model="policyTip" />
      <div>
        同意
        <a href="https://st.music.163.com/official-terms/service" target="_blank">《服务条款》</a>
        <a href="https://st.music.163.com/official-terms/privacy" target="_blank">《隐私政策》</a>
        <a href="https://st.music.163.com/official-terms/children" target="_blank">《儿童隐私政策》</a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "LoginIndex",
  computed: {
    policyTip: {
      get() {
        return this.$store.state.policyTip;
      },
      set(val) {
        this.$store.commit("changeValue", {
          type: "policyTip",
          value: val,
        });
      },
    },
  },
  methods: {
    login(type) {
      if (this.policyTip) {
        if (type === "login") {
          this.$emit("jumpLogin", true);
          this.$store.commit("changeValue", {
            type: "windowHead",
            value: "手机号登录",
          });
        } else if (type === "register") {
          this.$store.commit("changeValue", [
            {
              type: "registerShow",
              value: true,
            },
            {
              type: "windowHead",
              value: "手机号注册",
            },
          ]);
        }
      } else {
        this.$store.commit("changeValue", {
          type: "policyTipShow",
          value: true,
        });

        setTimeout(() => {
          this.$store.commit("changeValue", {
            type: "policyTipShow",
            value: false,
          });
        }, 2000);
      }
    },
    // phoneRegister() {
    //   if (this.policyTip) {
    //     this.$store.commit("changeValue", {
    //       type: "registerShow",
    //       value: true,
    //     });
    //   }else {

    //   }
    // },
  },
};
</script>
<style scoped>
input {
  margin-top: 2px;
  margin-right: 3px;
}
.bottom > div > a {
  color: #507daf;
  cursor: pointer;
}
.bottom > div > a:link {
  text-decoration: none;
}
.bottom > div > a:hover {
  border-bottom: solid 1px;
}
.bottom {
  font-size: 12px;
  color: #999;
  margin-left: 38px;
  margin-top: 40px;
}
.login,
.register {
  width: 224px;
  height: 31px;
  text-align: center;
  line-height: 31px;
  cursor: pointer;
  margin-top: 10px;
}
.register {
  background: linear-gradient(0deg, #f1f1f1, #fff);
  color: #333;
  border: solid 1px #c3c3c3;
  border-radius: 5px;
}
.register:hover {
  background: linear-gradient(0deg, #f8f8f8, #fff);
  border: solid 1px #c6c6c6;
}
.login {
  background: linear-gradient(0deg, #1d6ebe, #4493dc);
  color: #fff;
  border: solid 1px #0e65bc;
  border-radius: 5px;
}
.login:hover {
  background: linear-gradient(0deg, #3382ce, #5ea3e3);
  border: solid 1px #1860c8;
}
.left {
  width: 300px;
  height: 205px;
}
.left {
  margin-top: 40px;
  border-right: dotted 1px #ccc;
}
.right {
  margin-left: 40px;
  margin-top: 40px;
}
.left,
.right {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  align-items: center;
}
.top {
  display: flex;
}
.bottom {
  display: flex;
}
</style>