<template>
  <div>
    <main-tab-bar />
    <main-nav />
    <router-view />
    <popup-window />
    <tip-window />
    <operate-window />
    <policy-window />
    <create-window />
    <back-top />
    <music-audio />
    <foot />
  </div>
</template>

<script>
import MainTabBar from "./components/TabBar/MainTabBar";
import MainNav from "./components/Nav/MainNav";
import Foot from "./components/Foot/Foot";
import MusicAudio from "./views/MusicAudio";
import BackTop from "./components/BackTop/BackTop";
import PopupWindow from "./components/PopupWindow/PopupWindow";
import TipWindow from "./components/PopupWindow/TipWindow/TipWindow";
import OperateWindow from "./components/PopupWindow/OperateWindow/OperateWindow";
import policyWindow from "./components/PopupWindow/TipWindow/policyWindow";
import CreateWindow from "./components/PopupWindow/CreateWindow/CreateWndow";

import { request } from "./network/request";
export default {
  name: "App",
  components: {
    MainTabBar,
    MainNav,
    Foot,
    MusicAudio,
    BackTop,
    PopupWindow,
    TipWindow,
    OperateWindow,
    policyWindow,
    CreateWindow,
  },
  computed: {
    userPhone() {
      return this.$store.state.userPhone;
    },
    userPassword() {
      return this.$store.state.userPassword;
    },
    userAutoLogin() {
      return this.$store.state.userAutoLogin;
    },
  },
  created() {
    if (this.userAutoLogin) {
      let timestamp = new Date().getTime();
      request({
        url: `/login/cellphone?phone=${this.userPhone}&password=${this.userPassword}&timestamp=${timestamp}`,
      }).then((response) => {
        console.log(response);
        this.$store.commit("changeValue", [
          {
            type: "userToken",
            value: response.token,
          },
          {
            type: "userId",
            value: response.account.id,
          },
          {
            type: "userAvatarUrl",
            value: response.profile.avatarUrl,
          },
        ]);
      });
    }
  },
};
</script>

<style scoped>
div {
  background-color: #f5f5f5;
}
</style>